   @font-face{
        font-family: 'BebasNeueBook';  
        src: url('./font/BebasNeue Book.ttf');
  
        }
.Chill{
  height: 100vh;
  width: 100%;
    background: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  color: white;
}

.Chill_logo{
display: -ms-flexbox;
display: flex;
-ms-flex-pack: center;
    justify-content: center;  
color: white;
list-style: none;
font-size: 8em;
-webkit-transition: all 8s;
-o-transition: all 8s;
transition: all 8s;
opacity: 0;
margin-left: -5%;

}
.Chill_logo li{
text-shadow: 5px 40px 30px rgb(255, 255, 255);
-webkit-transition: all 6s;
-o-transition: all 6s;
transition: all 6s;
-webkit-transform: translate3d(0px, 0px, 0px);
        transform: translate3d(0px, 0px, 0px);
}
.Chill_logo li:first-child{
color: red;
}
.Chill_run li:first-child{
  -webkit-transform: translate3d(-50px, -80px, 0px);
          transform: translate3d(-50px, -80px, 0px);

}
.Chill_run li:nth-child(2){
  -webkit-transform: translate3d(0px, 80px, 0px);
          transform: translate3d(0px, 80px, 0px);
}
.Chill_run li:nth-child(4){
  -webkit-transform: translate3d(0px, -150px, 0px);
          transform: translate3d(0px, -150px, 0px);
}
.Chill_run li:nth-child(5){
  -webkit-transform: translate3d(120px, -0px, 0px);
          transform: translate3d(120px, -0px, 0px);
}
.Chill_autor{
font-size: 18px;
opacity: 0;
-webkit-transition: all 3s;
-o-transition: all 3s;
transition: all 3s;
width: auto;
}
.Chill_ball{
  width: 50px;
  height: 50px;
  border-radius: 50%;
-webkit-animation: Chill_ball 3s linear infinite alternate;
        animation: Chill_ball 3s linear infinite alternate;
  background: rgb(255, 255, 255);
  position: absolute;
opacity: 0;
}
.Chill_ball:first-child{
  left:10%;
}
.Chill_ball:nth-child(2){
    background: rgb(0, 209, 11);
    left:20%;
    top:10%;
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
}
.Chill_ball:nth-child(3){
    background: rgb(59, 56, 245);
     right:20%;
    top:20%;
      width: 50px;
  height: 50px;
        -webkit-animation-delay: 5.5s;
                animation-delay: 5.5s;
}
.Chill_ball:nth-child(4){
    background: rgb(151, 14, 144);
    bottom:30%;
         width: 30px;
  height: 30px;
  right: 10%;
      -webkit-animation-delay: 3.0s;
              animation-delay: 3.0s;
}
.Chill_button button{
  width: 35px;
  height: 35px;
  border-radius: 50%;
  opacity: 0.8;
}
.Chill_button{
  opacity: 0;
  -webkit-transition: all 3s;
  -o-transition: all 3s;
  transition: all 3s;
}
.Chill_button--play{
  background-image: url(./img/play-button.png);
      background-size: cover;
      background-repeat: no-repeat;
}
.Chill_button--stop{
    background-image: url(./img/stop-button.png);
      background-size:cover;
      background-repeat: no-repeat;
              background-position: center center; 
}
.Chill_button--before{
  background-image: url(./img/before-button.png);
      background-size: cover;
      background-repeat: no-repeat;
}
.Chill_button--after{
  background-image: url(./img/after-button.png);
      background-size: cover;
      background-repeat: no-repeat;
}
.Chill_button--stop{
  display: none;
}
.Chill_button_hide{

  width: 20px;
  height: 20px;
    background-image: url(./img/hide.png);
      background-size: cover;
      background-repeat: no-repeat;
 margin-left: 100%;
}
.Chill_autor{
  text-shadow: 5px 5px 3px rgb(255, 255, 255);
}
@media screen and (max-width: 465px) {
.Chill_logo{
font-size: 5em;

}
}
@-webkit-keyframes Chill_ball{
  from {
    background: rgb(0, 0, 0);
     width: 15px;
  height: 15px }
  to {    opacity: 1; }
}
@keyframes Chill_ball{
  from {
    background: rgb(0, 0, 0);
     width: 15px;
  height: 15px }
  to {    opacity: 1; }
}


